\n        border-left: 25px solid $white;\n        position: absolute;\n        left: 0;\n        top: 0;\n      }\n    }\n\n    // parent breadcrumb (links)\n    // iterate and darken the bg colours\n    @for $i from 1 through $breadcrumbColorsLength {\n      &:nth-of-type(1n+#{$i}) {\n        a {\n          /*color: $cyan;*/\n          color: $teal;\n          background-color: nth($breadcrumbColors, $i);\n\n          &:after {\n            border-left-color: nth($breadcrumbColors, $i);\n          }\n        }\n      }\n    }\n\n    // active breadcrumb (title)\n    span {\n      color: $gray-500;\n      background: lighten($gray-200, 5%);\n\n      &:after {\n        border-left-color: lighten($gray-200, 5%);\n      }\n    }\n\n    // first child remove arrow before\n    &:first-child a {\n      padding-left: 30px;\n\n      &:before {\n        display: none;\n      }\n    }\n  }\n}\n\nbody.without-banner {\n  .breadcrumbs-wrapper {\n    position: initial;\n  }\n  &.page-node-type-class {\n    .breadcrumbs-wrapper {\n      margin-top: -225px;\n    }\n  }\n}\n\n.page-node-type-camp {\n  .breadcrumbs-wrapper {\n    margin-top: -117px;\n  }\n}\n",".btn-transparent {\n  @include cachet();\n  letter-spacing: -1px;\n  background-color: transparent !important;\n  border: 3px solid white !important;\n  border-radius: 8px;\n  padding: 10px 25px;\n  line-height: 52px;\n  min-width: 180px;\n  color: $white;\n  text-transform: uppercase;\n\n  &:hover {\n    opacity: 0.7;\n  }\n}\n\na.back-link,\na.follow-link,\n.paragraph--type--grid-related-blog-posts .field-prgf-link a,\n.y-news-view-list .comments_count a {\n  @include cachet();\n  font-size: 20px;\n  letter-spacing: -1px;\n  line-height: 24px;\n  color: $primary;\n  text-transform: uppercase;\n\n  &:hover {\n    color: $pink;\n  }\n}\n\na.follow-link::after,\n.paragraph--type--grid-related-blog-posts .field-prgf-link a::after,\n.y-news-view-list .comments_count a::after {\n  font-family: 'icomoon' !important;\n  content: \