> span {\n    background: #fff;\n    color: $primary;\n    border-bottom: 1px solid $input-border-color;\n    border-top: 1px solid $input-border-color;\n\n    &:first-child {\n      border-left: 1px solid $input-border-color;\n    }\n\n    &:last-child {\n      border-right: 1px solid $input-border-color;\n    }\n  }\n\n  > .tabledrag-changed {\n    background: theme-color-level(\"warning\", -10);\n    border-radius: 0;\n    color: theme-color-level(\"warning\", 6);\n    display: table-cell;\n    padding: 0 1em;\n    top: 0;\n    vertical-align: middle;\n    border-left: 1px solid inherit;\n\n    &,\n    &:last-child {\n      border: 1px solid darken($warning, 5%);\n    }\n  }\n}\n\n.file-icon {\n  display: table-cell;\n  font-size: 150%;\n  padding: .25em .5em;\n  text-align: center;\n  vertical-align: middle;\n}\n\n.file-link {\n  display: table-cell;\n  vertical-align: middle;\n  width: 100%;\n\n  a,\n  a:hover,\n  a:focus,\n  a:active {\n    color: inherit;\n  }\n}\n\n.file-size {\n  display: table-cell;\n  padding: 0 1em;\n  text-align: right;\n  white-space: pre;\n  vertical-align: middle;\n}\n\n.image-widget.row {\n  overflow: hidden;\n}\n","/**\n * Filter styles.\n */\n\n// Mimic .panel-default styling.\n.filter-wrapper {\n  background-color: $white;\n  border: 1px solid $gray-300;\n  border-top: 0;\n  border-radius: 0 0 $border-radius $border-radius;\n  box-shadow: 0 1px 1px rgba($black, .05);\n  margin-bottom: 0;\n  padding: 10px;\n  height: 51px;\n}\n\n.filter-help {\n  float: right;\n  line-height: 1;\n  margin: .5em 0 0;\n}\n\n// Full list page.\n.nav.nav-tabs.filter-formats {\n  margin-bottom: 15px;\n}\n","/**\n * Form styles.\n */\n\n::-webkit-input-placeholder,\n::-moz-placeholder,\n:-ms-input-placeholder,\ninput:-moz-placeholder {\n  font-style: italic;\n}\n\n//.form-type-checkbox,\n.form-type-radio {\n  display: flex;\n  align-items: center;\n\n  label {\n    margin: 0;\n  }\n}\n\n// Checkboxes / radio buttons.\n.checkbox, .radio {\n  // Remove top/bottom margins when in table context, because this is most\n  // likely a tableselect checkbox element.\n  table &.form-no-label {\n    margin-bottom: 0;\n    margin-top: 0;\n  }\n}\n\n// Browsers do not recognize pseudo :after selectors, we must create a wrapper\n// around the select element to style it properly.\n// $see http://stackoverflow.com/q/21103542\n.select-wrapper {\n  display: inline-block;\n  width: 100%;\n  text-align: center;\n\n  .form-inline & {\n    width: auto;\n  }\n\n  .input-group & {\n    display: table-cell;\n\n    &:first-child .form-control:first-child {\n      @include border-left-radius($border-radius);\n    }\n\n    &:last-child .form-control:first-child {\n      @include border-right-radius($border-radius);\n    }\n  }\n\n  select {\n    appearance: none;\n    line-height: 1;\n    padding-right: 2em;\n\n    &::-ms-expand {\n      opacity: 0;\n    }\n  }\n\n  // arrow down\n  i {\n    color: $primary;\n    position: absolute;\n    top: 6px;\n    right: 10px;\n    font-size: 26px;\n\n    .has-error & {\n      color: theme-color-level(\"danger\", -10);\n    }\n\n    .has-success & {\n      color: theme-color-level(\"success\", -10);\n    }\n\n    .has-warning & {\n      color: theme-color-level(\"warning\", -10);\n    }\n  }\n}\n\n// Use CSS/SVG image for required mark.\n// @see https://www.drupal.org/node/2152217\n// @see https://www.drupal.org/node/2274631\n.form-required:after {\n  background-image: url(../img/required.svg);\n  background-size: 10px 7px;\n  content:\"\";\n  display: inline-block;\n  line-height:1;\n  height: 7px;\n  width: 10px;\n  vertical-align: text-top;\n}\n\n// Form items.\n.form-actions,\n.form-item,\n.form-text {\n  margin: 0;\n}\n\n// Form action buttons.\n.form-actions {\n  .btn, .btn-group {\n    @include cachet-book();\n    margin-left: 10px;\n  }\n\n  .btn-group {\n    .btn {\n      margin-left: 0;\n    }\n  }\n}\n\n.btn-primary {\n  background-color: $dark-blue;\n  border-color: $dark-blue;\n}\n","/**\n * Icon styles.\n */\na {\n  &.icon-before .glyphicon {\n    margin-right: .25em;\n  }\n\n  &.icon-after .glyphicon {\n    margin-left: .25em;\n  }\n}\n\n.btn {\n  &.icon-before .glyphicon {\n    margin-left: -.25em;\n    margin-right: .25em;\n  }\n\n  &.icon-after .glyphicon {\n    margin-left: .25em;\n    margin-right: -.25em;\n  }\n}\n","// Inline turns list items into inline-block\n.list-inline li {\n  display: inline-block;\n\n  &:not(:last-child) {\n    margin-right: $list-inline-padding;\n  }\n}","/**\n * Navbar styling.\n */\nbody {\n  // Fix horizontal scrolling on iOS devices.\n  // http://drupal.org/node/1870076\n  position: relative;\n\n  &.navbar-is-static-top {\n    margin-top: 0;\n  }\n\n  @include media-breakpoint-up(md) {\n    margin-top: ($grid-gutter-width / 2);\n  }\n\n  @include media-breakpoint-up(md) {\n    &.toolbar-vertical {\n      &.navbar-is-fixed-top,\n      &.navbar-is-fixed-bottom {\n        .toolbar-bar {\n          position: fixed;\n        }\n\n        header {\n          z-index: 500;\n        }\n      }\n\n      // Default toolbar fixed height value.\n      // @see core/modules/toolbar/css/toolbar.icons.theme.css@261\n      &.navbar-is-fixed-top header {\n        top: 39px;\n      }\n    }\n  }\n}","/**\n * Node styling.\n */\n\n.node-preview-container {\n  margin-top: -($grid-gutter-width / 2);\n}\n\n.node-preview-form-select {\n  padding: ($grid-gutter-width / 2);\n}\n","/**\n * Panel styling.\n */\n.panel-title {\n  display: block;\n  margin: -10px -15px;\n  padding: 10px 15px;\n\n  &, &:hover, &:focus, &:hover:focus {\n    color: inherit;\n  }\n\n  &:focus, &:hover {\n    text-decoration: none;\n  }\n}\n","/**\n * Progress bar styles.\n */\n.progress-wrapper {\n  margin-bottom: $form-group-margin-bottom;\n\n  &:last-child .progress {\n    margin-bottom: 5px;\n  }\n\n  .message {\n    font-weight: 700;\n    margin-bottom: 5px;\n  }\n\n  .percentage,\n  .progress-label {\n    font-size: $font-size-sm;\n  }\n\n  .progress-bar {\n    min-width: 2em;\n  }\n}\n","/**\n * Table drag styles.\n */\n.tabledrag-toggle-weight {\n  float: right;\n  margin: 1px 2px 1px 10px;\n}\n\n.tabledrag-changed-warning {\n  margin: 0;\n  overflow: hidden;\n}\n\n.tabledrag-handle {\n  color: $gray-200;\n  cursor: move;\n  float: left;\n  font-size: 125%;\n  line-height: 1;\n  margin: -10px 0 0 -10px;\n  padding: 10px;\n\n  &:hover, &:focus {\n    color: $primary;\n  }\n}\n\n.indentation {\n  float: left; /* LTR */\n  height: 1.7em;\n  margin: -0.4em 0.2em -0.4em -0.4em; /* LTR */\n  padding: 0.42em 0 0.42em 0.6em; /* LTR */\n  width: 20px;\n\n  [dir=\"rtl\"] & {\n    float: right;\n    margin: -0.4em -0.4em -0.4em 0.2em;\n    padding: 0.42em 0.6em 0.42em 0;\n  }\n}\n","/**\n * Tabs and local action styles.\n */\n.local-actions {\n  margin: 10px 0 10px -5px;\n}\n\n.tabs--secondary {\n  margin: 10px 0 5px;\n}\n\n/**\n * Missing Bootstrap 2 tab styling.\n * $see http://stackoverflow.com/questions/18432577/stacked-tabs-in-bootstrap-3\n * $see http://bootply.com/74926\n */\n.tabbable {\n  margin-bottom: 20px;\n}\n\n.tabs-below, .tabs-left, .tabs-right {\n  > .nav-tabs {\n    border-bottom: 0;\n\n    .summary {\n      color: $nav-link-disabled-color;\n      font-size: $font-size-sm;\n    }\n  }\n}\n\n.tab-pane {\n\n  > .panel-heading {\n    display: none;\n  }\n\n  .field-location-address {\n    margin-top: 20px;\n  }\n\n  a {\n    color: inherit;\n  }\n}\n\n.tab-content > .active {\n  display: block;\n}\n\n// Below.\n.tabs-below {\n  > .nav-tabs {\n    border-top: 1px solid $nav-tabs-border-color;\n\n    > li {\n      margin-top: -1px;\n      margin-bottom: 0;\n\n      > a {\n        border-radius: 0 0 $border-radius $border-radius;\n\n        &:hover,\n        &:focus {\n          border-top-color: $nav-tabs-border-color;\n          border-bottom-color: transparent;\n        }\n      }\n    }\n\n    > .active {\n      > a,\n      > a:hover,\n      > a:focus {\n        border-color: transparent $nav-tabs-border-color $nav-tabs-border-color $nav-tabs-border-color;\n      }\n    }\n  }\n}\n\n// Left and right tabs.\n.tabs-left,\n.tabs-right {\n  > .nav-tabs {\n    padding-bottom: 20px;\n    width: 220px;\n\n    > li {\n      float: none;\n\n      &:focus {\n        outline: 0;\n      }\n\n      > a {\n        margin-right: 0;\n        margin-bottom: 3px;\n\n        &:focus {\n          outline: 0;\n        }\n      }\n    }\n  }\n\n  > .tab-content {\n    border-radius: 0 $border-radius $border-radius $border-radius;\n    border: 1px solid $nav-tabs-border-color;\n    box-shadow: 0 1px 1px rgba(0,0,0,.05);\n    overflow: hidden;\n    padding: 10px 15px;\n  }\n}\n\n// Left tabs.\n.tabs-left {\n  > .nav-tabs {\n    float: left;\n    margin-right: -1px;\n\n    > li > a {\n      border-radius: $border-radius 0 0 $border-radius;\n\n      &:hover,\n      &:focus {\n        border-color: $nav-tabs-link-hover-border-color;\n      }\n    }\n\n    > .active > a,\n    > .active > a:hover,\n    > .active > a:focus {\n      border-color: $nav-tabs-border-color;\n      box-shadow: -1px 1px 1px rgba(0,0,0,.05);\n    }\n  }\n}\n\n// Right tabs.\n.tabs-right {\n  > .nav-tabs {\n    float: right;\n    margin-left: -1px;\n\n    > li > a {\n      border-radius: 0 $border-radius $border-radius 0;\n\n      &:hover,\n      &:focus {\n        border-color: $nav-tabs-link-hover-border-color;\n        box-shadow: 1px 1px 1px rgba(0,0,0,.05);\n      }\n    }\n    > .active > a,\n    > .active > a:hover,\n    > .active > a:focus {\n      border-color: $nav-tabs-border-color $nav-tabs-border-color $nav-tabs-border-color transparent;\n    }\n  }\n}\n","/**\n * Toolbar module styling.\n */\n\n$toolbar-width: 240px;\n\nbody.toolbar-fixed {\n\n  // Fix z-index.\n  .toolbar-oriented .toolbar-bar {\n    z-index: ($zindex-fixed + 1);\n  }\n\n  .fixed-top {\n    top: 39px;\n\n    &.mobile-sidebar {\n      top: 109px;\n    }\n  }\n\n  // Horizontal.\n  &.toolbar-horizontal.toolbar-tray-open .fixed-top {\n    top: 79px;\n\n    &.mobile-sidebar {\n      top: 149px;\n    }\n  }\n\n  // Vertical Open.\n  &.toolbar-vertical.toolbar-tray-open {\n    .fixed-top {\n      left: $toolbar-width;\n    }\n\n    &.toolbar-fixed {\n      margin-left: $toolbar-width;\n\n      .toolbar-tray {\n        padding-bottom: 40px;\n\n        &, > .toolbar-lining:before {\n          width: $toolbar-width;\n        }\n      }\n    }\n  }\n}\n","/**\n * jQuery UI autocomplete widget style overrides.\n *\n * $todo Remove once jQuery UI is no longer used?\n */\n\n// Mimics .dropdown-menu styles.\n.ui-autocomplete {\n  background: $dropdown-bg;\n  background-clip: padding-box;\n  border: 1px solid $dropdown-border-color;\n  border-radius: $border-radius;\n  box-shadow: 0 6px 12px rgba($black, .175);\n  color: inherit;\n  font-family: $font-family-base;\n  font-size: $font-size-base;\n  list-style: none;\n  min-width: 160px;\n  padding: 5px 0;\n  text-align: left;\n  z-index: $zindex-dropdown;\n\n  .ui-menu-item {\n    border: 0;\n    border-radius: 0;\n    clear: both;\n    color: $dropdown-link-color;\n    cursor: pointer;\n    display: block;\n    font-weight: normal;\n    line-height: $line-height-base;\n    margin: 0;\n    outline: 0;\n    padding: 3px 20px;\n    text-decoration: none;\n    white-space: nowrap;\n\n    &.ui-state-hover {\n      background: $dropdown-link-hover-bg;\n      color: $dropdown-link-hover-color;\n    }\n\n    &.ui-state-active, &.ui-state-focus {\n      background: $dropdown-link-active-bg;\n      color: $dropdown-link-active-color;\n    }\n  }\n}\n",".addthis {\n  margin-top: 10px;\n}\n",".activity-details-modal-content {\n  .col-md-6 {\n    @include media-breakpoint-down(md) {\n      flex: 0 0 100%;\n      max-width: 100%;\n    }\n  }\n}\n\n.select-path-component {\n  .path.btn {\n    .text-uppercase {\n      @include media-breakpoint-down(md) {\n        font-size: 14px;\n      }\n    }\n  }\n}\n",".block-description {\n  &--text {\n\n    h2 {\n      @include media-breakpoint-up(sm) {\n        font-size: 26px;\n      }\n\n      @include media-breakpoint-up(md) {\n        font-size: 30px;\n      }\n\n      &.with-icon {\n        padding-left: 28px;\n\n        @include media-breakpoint-up(sm) {\n          padding-left: 32px;\n        }\n\n        @include media-breakpoint-up(md) {\n          padding-left: 40px;\n        }\n      }\n    }\n\n    h4 {\n      @include cachet();\n      font-size: 26px;\n      color: $purple;\n      margin: 0 0 10px;\n      vertical-align: middle;\n\n      @include media-breakpoint-up(md) {\n        font-size: 36px;\n      }\n    }\n\n    .fa {\n      color: $purple;\n      font-size: 24px;\n      vertical-align: middle;\n      margin-right: 10px;\n      max-height: 24px;\n      max-width: 24px;\n      float: left;\n      width: auto;\n\n      @include media-breakpoint-up(md) {\n        font-size: 26px;\n        max-height: 26px;\n        max-width: 26px;\n      }\n\n      @include media-breakpoint-up(lg) {\n        font-size: 30px;\n        max-height: 30px;\n        max-width: 30px;\n      }\n    }\n\n    .field-icon {\n      vertical-align: middle;\n      margin-right: 5px;\n      float: left;\n\n      img {\n        vertical-align: middle;\n        max-height: 24px;\n        max-width: 24px;\n        width: auto;\n\n        @include media-breakpoint-up(md) {\n          max-height: 28px;\n          max-width: 28px;\n        }\n\n        @include media-breakpoint-up(lg) {\n          max-height: 35px;\n          max-width: 35px;\n        }\n      }\n    }\n\n    p {\n      font-size: 13px;\n      margin: 0;\n\n      @include media-breakpoint-up(md) {\n        font-size: 15px;\n      }\n    }\n\n    .content {\n      margin: 0 0 10px 0;\n    }\n\n    .field-sb-link {\n      @include read-more-link();\n    }\n\n    .field-sb-body {\n      @include media-breakpoint-up(md) {\n        font-size: 15px;\n      }\n    }\n  }\n\n  &--3 {\n    margin-bottom: 3rem;\n\n    .block-description--text {\n      padding-top: 30px;\